}

.cssload_loro {
	position: relative;
	width: 225px;
	height: 292px;
	margin: 0 auto;
}

.cssload_circ {
	position: absolute;
	width: 118px;
	height: 207px;
	border-radius: 292px 0 0 292px;
	background: rgb(37,177,176);
	margin: 29px 50%;
	transform-origin: 50% 50%;
